@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,900;1,600;1,700&display=swap);
.btn-wrapper{
    position: relative;
}

.btn-wrapper>.linkto{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
    position: absolute;
    top: -10px;
    left: -10px;
    font-weight: bold;
    color: black;
    text-decoration: none;
    border: none;
    padding: 10px;
    background-color: #FF5151;
    transition: .3s;
}

.btn-wrapper>.linkto:hover{
    transition: .3s;
    top: -4px;
    left: -4px;
    color: white
}
.student-detail{
    padding: 150px 0 150px 0;
    width: 40vw;
    min-width: 240px;
    margin: 0 auto;
}

.student-detail-class{
    font-weight: bold;
    display: inline;
}

.student-detail img{
    position: relative;
    width: 100%;
    height: 40vw;
    min-height: 270px;
    object-fit: cover;
    margin-bottom: 40px;
}

.student-detail-biodata-title{
    font-weight: bold;
    margin-bottom: 0;
    font-size: 14px !important;
}

.student-detail-biodata p{
    font-size: 16px;
}

.student-detail-call{
    margin-top: 50px;
}
